                  <v-text-field v-model="editedItem.name" label="Point name" required :rules="[() => !isExistingvalue(editedItem, 'name') || 'Name Existed',rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-checkbox v-model="editedItem.enable" label="Enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-select v-model="editedItem.value_operation" :items="value_operation" :label="toUpperTextKey('value_operation')"></v-select>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.cov_threshold" :label="toUpperTextKey('cov_threshold')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.value_original" :label="toUpperTextKey('value_original')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.value_round" :label="toUpperTextKey('value_round')" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4" md="4">
